
<template>
  <div
    v-if="currentInfo"
    style="background-color:#F6F6F6;position:fixed;top:50px;bottom:0;left:0;right:0;overflow-y:scroll;"
  >
    <div class="box_1">
      <div class="box_1_1">
        <div>账单状态</div>
        <div>
          <span :class="currentInfo.status == 0?'f88':'f02'">{{ currentInfo.status == 0?'待提交':'已提交' }}</span>
          <span
            v-if="currentInfo.status == 1"
            :class="currentInfo.payStatus == 0?'c_g':'f02'"
          >{{ currentInfo.payStatus == 0?"未到账":"已到账" }}</span>
        </div>
      </div>
      <div class="box_1_1">
        <div>收租日期</div>
        <div>{{ currentInfo.endTime }}</div>
      </div>
      <div class="box_1_1">
        <div>缴费周期</div>
        <div>
          <span v-if="set_round">第{{ set_round }}期</span>
          {{ currentInfo.startTime?currentInfo.startTime.substr(0,10):"" }}&ensp;至&ensp;{{ currentInfo.endTime?currentInfo.endTime.substr(0,10):"" }}
        </div>
      </div>
      <div
        class="box_1_1"
        v-if="currentInfo.payTime"
      >
        <div>到账日期</div>
        <div>{{ currentInfo.payTime.substr(0,10) }}</div>
      </div>
      <div
        class="box_1_1"
        v-if="currentInfo.payment"
      >
        <div>支付方式</div>
        <div>{{ payment_label }}</div>
      </div>
      <div
        class="box_1_1"
        v-if="currentInfo.payment > '5' && currentInfo.payeeAccount"
      >
        <div>收款账户</div>
        <div>{{ currentInfo.payeeAccount }}</div>
      </div>
    </div>
    <div class="box_1 mb20">
      <div class="box_1_1">
        <div class="f88">费用明细</div>
        <div
          v-if="currentInfo.status == 0 && set_round == set_length"
          class="red"
          @click="LiveEdit"
        >编辑</div>
      </div>
      <div
        class="box_1_1"
        v-if="currentInfo.elecPrice > 0 || $route.query.cooperLifeCostBillId"
      >
        <div class="box_left">
          <span>
            电费
            <span
              class="c_88"
              v-if="currentInfo.elecMode == '1'"
            >{{ currentInfo.elecPrice?currentInfo.elecPrice.toFixed(2):'0' }}/度</span>
          </span>
          <br />
          <span
            class="c_88"
            v-if="currentInfo.elecMode == '1' && currentInfo.preElecDegree && currentInfo.elecDegree"
          >本期用电：{{ currenEle | point }}度（{{ currentInfo.preElecDegree || 0 }}-{{ currentInfo.elecDegree?currentInfo.elecDegree:currentInfo.preElecDegree }}）</span>
          <span
            class="c_88"
            v-else-if="currentInfo.elecMode == '1' && currentInfo.preElecDegree && !currentInfo.elecDegree"
          >本期用电：0度</span>
        </div>
        <!-- 电-价格 -->
        <div v-if="$route.query.cooperLifeCostBillId && currenEle">
          <div>{{ currentInfo.elecPrice?(currentInfo.elecPrice * currenEle).toFixed(2):'' }}元</div>
        </div>
        <div v-else-if="currentInfo.elecMode == '2' || currentInfo.elecMode == '3'">{{ currentInfo.elecMode == '2'?(currentInfo.elecPrice).toFixed(2):currentInfo.elecPrice }}{{ currentInfo.elecMode == '2'?'元':'度' }}</div>
        <div v-else-if="!$route.query.cooperLifeCostBillId">{{ currentInfo.elecMode == '1'?elecTPrice.toFixed(2):currentInfo.elecPrice.toFixed(2) }}元</div>
        <div v-else>0</div>
      </div>
      <div
        class="box_1_1"
        v-if="currentInfo.waterPrice > 0 || $route.query.cooperLifeCostBillId"
      >
        <div class="box_left">
          <span>
            水费
            <span
              class="c_88"
              v-if="currentInfo.waterMode == '1'"
            >{{ currentInfo.waterPrice?currentInfo.waterPrice.toFixed(2):'0' }}/吨</span>
          </span>
          <br />
          <span
            class="c_88"
            v-if="currentInfo.waterMode == '1' && currentInfo.preWaterDegree && currentInfo.waterDegree"
          >本期用水：{{ currenWater | point }}吨（{{ currentInfo.preWaterDegree || 0}}-{{ currentInfo.waterDegree?currentInfo.waterDegree:currentInfo.preWaterDegree }}）</span>
          <span
            class="c_88"
            v-else-if="currentInfo.waterMode == '1' && currentInfo.preWaterDegree && !currentInfo.waterDegree"
          >本期用水：0吨</span>
        </div>
        <!-- 水-价格 -->
        <div v-if="$route.query.cooperLifeCostBillId && currenWater">
          <div>{{ currentInfo.waterPrice?(currentInfo.waterPrice*currenWater).toFixed(2):'' }}元</div>
        </div>
        <div v-else-if="currentInfo.waterMode == '2' || currentInfo.waterMode == '3'">{{ currentInfo.waterMode == '2'?(currentInfo.waterPrice).toFixed(2):currentInfo.waterPrice }}{{ currentInfo.waterMode == '2'?'元':'吨' }}</div>
        <div v-else-if="!$route.query.cooperLifeCostBillId">{{ currentInfo.waterMode == '1'?waterTPrice.toFixed(2):currentInfo.waterPrice.toFixed(2) }}元</div>
        <div v-else>0</div>
      </div>
      <div
        class="box_1_1"
        v-if="currentInfo.gasPrice > 0 || $route.query.cooperLifeCostBillId"
      >
        <div class="box_left">
          <span>
            气费
            <span
              class="c_88"
              v-if="currentInfo.gasMode == '1'"
            >{{ currentInfo.gasPrice?currentInfo.gasPrice.toFixed(2):'0' }}/m³</span>
          </span>
          <br />
          <span
            class="c_88"
            v-if="currentInfo.gasMode == '1' && currentInfo.preGasDegree && currentInfo.gasDegree"
          >本期用气：{{ currenGas | point }}m³（{{ currentInfo.preGasDegree || 0 }}-{{ currentInfo.gasDegree?currentInfo.gasDegree:currentInfo.preGasDegree }}）</span>
          <span
            class="c_88"
            v-else-if="currentInfo.gasMode == '1' && currentInfo.preGasDegree && !currentInfo.gasDegree"
          >本期用气：0m³</span>
        </div>
        <!-- 气-价格 -->
        <div v-if="$route.query.cooperLifeCostBillId && currenGas">
          <div>{{ currentInfo.gasPrice?(currentInfo.gasPrice*currenGas).toFixed(2):'' }}元</div>
        </div>
        <div v-else-if="currentInfo.gasMode == '2' || currentInfo.gasMode == '3'">{{ currentInfo.gasMode == '2'?(currentInfo.gasPrice).toFixed(2):currentInfo.gasPrice }}{{ currentInfo.gasMode == '2'?'元':'m³' }}</div>
        <div v-else-if="!$route.query.cooperLifeCostBillId">{{ currentInfo.gasMode == '1'?gasTPrice.toFixed(2):currentInfo.gasPrice.toFixed(2) }}元</div>
        <div v-else>0</div>
      </div>
      <div class="box_1_1">
        <div class="f88">清洁费(元/月)</div>
        <div v-if="!$route.query.cooperLifeCostBillId">{{ currentInfo.rubbishPrice?currentInfo.rubbishPrice:0 }}</div>
        <div v-else>
          <div>{{ currentInfo.rubbishPrice?currentInfo.rubbishPrice:0 }}</div>
        </div>
      </div>
      <div class="box_1_1">
        <div class="f88">停车费(元/月)</div>
        <div v-if="!$route.query.cooperLifeCostBillId">{{ currentInfo.parkPrice?currentInfo.parkPrice+'元':0 }}</div>
        <div v-else>
          <div>{{ currentInfo.parkPrice?currentInfo.parkPrice+'元':0 }}</div>
        </div>
      </div>
      <div class="box_1_1">
        <div class="f88">物业费(元/月)</div>
        <div v-if="!$route.query.cooperLifeCostBillId">{{ currentInfo.propPrice?currentInfo.propPrice+'元':0 }}</div>
        <div v-else>
          <div>{{ currentInfo.propPrice?currentInfo.propPrice+'元':0 }}</div>
        </div>
      </div>
      <div class="box_1_1">
        <div class="f88">宽带费(元/月)</div>
        <div v-if="!$route.query.cooperLifeCostBillId">{{ currentInfo.broadBandPrice?currentInfo.broadBandPrice+'元':0 }}</div>
        <div v-else>
          <div>{{ currentInfo.broadBandPrice?currentInfo.broadBandPrice+'元':0 }}</div>
        </div>
      </div>
      <!-- 换房记录 -->
      <div
        style="margin:20px 0;"
        v-if="recordRoom.length > 0 || recordWatch.length > 0"
      >
        <div v-if="recordRoom.length > 0">
          <div
            class="box_1"
            v-for="item in recordRoom"
            :key="item.id"
          >
            <p class="settlement">
              换房结算&emsp;&emsp;&emsp;&ensp;
              <span>{{ item.preRoomSn }}➜{{ item.roomSn }}</span>
            </p>
            <div
              class="last cost"
              v-if="currentInfo.elecPrice > 0"
            >
              <p class="c_00">电费结算</p>
              <p class="c_00">{{ Number(item.ele_totalPrice).toFixed(2) }}元</p>
              <p>起始：{{ item.preElecDegree?item.preElecDegree:0 }}</p>
              <p>结算：{{ item.elecDegree?item.elecDegree:0 }}</p>
            </div>
            <div
              class="last cost"
              v-if="currentInfo.waterPrice > 0"
            >
              <p class="c_00">水费结算</p>
              <p class="c_00">{{ item.water_totalPrice }}元</p>
              <p>起始：{{ item.preWaterDegree?item.preWaterDegree:0 }}</p>
              <p>结算：{{ item.waterDegree?item.waterDegree:0 }}</p>
            </div>
            <div
              class="last cost"
              v-if="currentInfo.gasPrice > 0"
            >
              <p class="c_00">气费结算</p>
              <p class="c_00">{{ item.gas_totalPrice }}元</p>
              <p>起始：{{ item.preGasDegree?item.preGasDegree:'0' }}</p>
              <p>结算：{{ item.gasDegree?item.gasDegree:'0' }}</p>
            </div>
          </div>
        </div>
        <!-- 换表记录 -->
        <div v-if="recordWatch.length > 0">
          <div
            class="box_1"
            v-for="item in recordWatch"
            :key="item.id"
          >
            <p class="settlement">
              换表结算&emsp;&emsp;&emsp;&ensp;
            </p>
            <div
              class="last cost"
              v-if="item.meterType == 1"
            >
              <p class="c_00">电费结算</p>
              <p class="c_00">{{ Number(item.ele_totalPrice).toFixed(2) }}元</p>
              <p>起始：{{ item.preElecDegree?item.preElecDegree:0 }}</p>
              <p>结算：{{ item.elecDegree?item.elecDegree:0 }}</p>
            </div>
            <div
              class="last cost"
              v-if="item.meterType == 2"
            >
              <p class="c_00">水费结算</p>
              <p class="c_00">{{ item.water_totalPrice }}元</p>
              <p>起始：{{ item.preWaterDegree?item.preWaterDegree:0 }}</p>
              <p>结算：{{ item.waterDegree?item.waterDegree:0 }}</p>
            </div>
            <div
              class="last cost"
              v-if="item.meterType == 3"
            >
              <p class="c_00">气费结算</p>
              <p class="c_00">{{ item.gas_totalPrice }}元</p>
              <p>起始：{{ item.preGasDegree?item.preGasDegree:'0' }}</p>
              <p>结算：{{ item.gasDegree?item.gasDegree:'0' }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="box_2 position">
      <div class="leftTile">
        <div class="price">
          <span
            class="f12"
            style="color:#ccc;"
          >费用总计</span>
          <br />
          <span
            class="t_price"
            v-if="currentInfo.payStatus == 0 && set_round == set_length"
          >&yen;{{ currentInfo.totalPrice?currentInfo.totalPrice.toFixed(2):'0' }}</span>
          <span
            class="t_price"
            v-else
          >&yen;{{ currentInfo.totalPrice?currentInfo.totalPrice.toFixed(2):'0' }}</span>
        </div>
      </div>
      <div v-if='!$route.query.cooperLifeCostBillId'>
        <van-button
          v-if="currentInfo.payStatus == 0 && currentInfo.status == 1 && set_round != set_length"
          type="primary"
          class="button payS_r"
          @click="showDelete = true"
        >作废</van-button>
        <van-button
          v-if="currentInfo.payStatus == 0 && currentInfo.status == 1"
          type="primary"
          class="button button_f98"
          @click="getCode(currentInfo)"
        >收款码</van-button>
        <van-button
          v-if="currentInfo.payStatus == 0 && currentInfo.status == 1 && is_businessPayOffline == true"
          type="primary"
          class="button payS_r"
          @click="comeShow = true"
        >线下收款</van-button>
        <van-button
          v-if="currentInfo.status == 1 && currentInfo.payStatus == 1"
          type="primary"
          class="button payS_w"
        >已到账</van-button>
        <van-button
          v-if="currentInfo.payStatus == 0 && set_round == set_length && currentInfo.status != 0"
          type="primary"
          class="button button_f00"
          @click="backBill"
        >撤回账单</van-button>
      </div>
      <div v-else>
        <van-button
          v-if="currentInfo.status ==1 && currentInfo.payStatus == 0 && is_businessPayOffline == true"
          type="primary"
          class="button payS_r"
          style="border-color:#ccc;"
          @click="comeShow = true"
        >线下收款</van-button>
        <van-button
          v-if="currentInfo.status ==1 && currentInfo.payStatus == 1"
          type="primary"
          class="button"
          style="background:#b2b2b2;color:#fff;border:#b2b2b2;"
        >已到账</van-button>
        <van-button
          v-if="set_round == set_length && currentInfo.status == 1"
          type="primary"
          class="button button_f00"
          @click="backBillCooper"
        >撤回账单</van-button>
      </div>
    </div>
    <!-- 到账 -->
    <van-popup
      v-model="comeShow"
      class="show-box"
    >
      <div class="show-box-content">
        <p class="f22">到账</p>
        <div class="pay-room none">
          <span>到账时间</span>
          <div class="border">
            <input
              @click="comeShowTime = true"
              class="center"
              readonly
              placeholder="请选择"
              type="text"
              v-model="comeTime"
            >
          </div>
        </div>
        <div class="pay-room none">
          <span>收款账户</span>
          <div
            class="border display"
            @click="users.length > 0?payeeAccountIdValueShow = true:''"
          >
            <input
              class="center"
              readonly
              placeholder="请选择收款账户"
              type="text"
              v-model="payeeAccountIdValue"
            >
            <van-icon name="arrow-down" />
          </div>
        </div>
        <van-button
          class="show-button"
          @click="mySave"
        >确定</van-button>
      </div>
    </van-popup>
    <!-- 选择到账时间 -->
    <van-calendar
      v-model="comeShowTime"
      :max-date="maxDate"
      :min-date="minDate"
      @confirm="onConfirmTime"
    />
    <!-- 收款账户 -->
    <van-popup
      style="width: 100%"
      v-model="payeeAccountIdValueShow"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="users"
        @confirm="onConfirmUser"
        @cancel="payeeAccountIdValueShow = false"
      />
    </van-popup>
    <van-dialog
      v-model="showDelete"
      title="账单作废"
      show-cancel-button
      :before-close="confirmDelete"
    >
      <van-field
        v-model="deleteRemarks"
        placeholder="请输入备注"
      />
    </van-dialog>
  </div>
</template>
<script>
import { Toast, MessageBox } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import {
    GetBusinessLifeCostBill,
    GetBusinessLifeCostRoomRecordByLifeCostBill,
    PostBusinessLifeCostBillEdit,
    PostBusinessLifeCostBillPayOffline,
    PostBusinessLifeCostBillRecall,
    PostBusinessLifeCostBillRemove
} from '@/api/api_business_hotel_and_scale';
import { GetBusinessHotelPayeeAccountByhotel, PostBusinessCoopeLifeCostBillPayOffline, GetBusinessCooperLifeCostBill, PostBusinessCooperLifeCostBillRecall, PostBusinessCooperLifeCostBillSubmit } from '@/api/api_business';
import { isPermission } from '@/utils/filters';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            currentInfo: '',
            set_length: '',
            set_round: '',
            lifeInfo: '',
            ele_unit: '',
            life_unit: '',
            currenEle: '',
            currenWater: '',
            recordInfo: [],
            payments: [],
            recordRoom: [],
            recordWatch: [],
            users: [],
            roomT: 0,
            waterT: 0,
            currenGas: '',
            comeShowTime: false,
            payeeAccountIdValueShow: false,
            maxDate: new Date(),
            minDate: new Date(),
            date: '',
            paymentsColumns: [],
            comeTime: dayjs().format('YYYY-MM-DD'),
            dictLabel: null,
            payeeAccountIdValue: null,
            comeShow: false,
            is_businessPayOffline: true,
            showDelete: false,
            payment_label: null,
            deleteRemarks: null
        };
    },
    created() {
        this.$store.commit('setHeader', '账单明细');
        this.is_businessPayOffline = isPermission('business:pay:offline');
        this.hotelId = localStorage.getItem('c_hotelId');
        this.set_length = Number(this.$route.query.set_length);
        this.set_round = Number(this.$route.query.set_round) + 1;
        this.minDate = new Date(dayjs().subtract(2, 'month'));
        this.comeTime = dayjs().format('YYYY-MM-DD');
        if (this.$route.query.cooperLifeCostBillId) {
            this.getCooperCurrentInfo(this.$route.query.cooperLifeCostBillId);
        } else {
            this.getCurrentInfo(this.$route.query.lifeCostBillId);
        }
    },
    computed: {
        elecTPrice() {
            return this.currenEle * (this.currentInfo.elecPrice ? this.currentInfo.elecPrice.toFixed(2) : '0');
        },
        waterTPrice() {
            return this.currenWater * (this.currentInfo.waterPrice ? this.currentInfo.waterPrice.toFixed(2) : '0');
        },
        gasTPrice() {
            return this.currenGas * (this.currentInfo.gasPrice ? this.currentInfo.gasPrice.toFixed(2) : '0');
        },
        setTotalPrice() {
            return (
                Number(this.currentInfo.elecPrice) +
                Number(this.currentInfo.waterPrice) +
                Number(this.currentInfo.gasPrice) +
                Number(this.currentInfo.rubbishPrice) +
                Number(this.currentInfo.parkPrice) +
                Number(this.currentInfo.propPrice) +
                Number(this.roomT) +
                Number(this.waterT)
            );
        }
    },
    methods: {
        getCooperCurrentInfo(id) {
            GetBusinessCooperLifeCostBill({ cooperLifeCostBillId: id }).done((res) => {
                this.currentInfo = res.data;
                if (JSON.parse(localStorage.getItem('life_fee'))) {
                    this.localInfo = JSON.parse(localStorage.getItem('life_fee'));
                    (this.currentInfo.waterDegree = this.localInfo.waterDegree),
                        (this.currentInfo.elecDegree = this.localInfo.elecDegree),
                        (this.currentInfo.gasDegree = this.localInfo.gasDegree),
                        (this.currentInfo.parkPrice = this.localInfo.parkPrice),
                        (this.currentInfo.propPrice = this.localInfo.propPrice),
                        (this.currentInfo.rubbishPrice = this.localInfo.rubbishPrice);
                }
                if (this.currentInfo.elecDegree - this.currentInfo.preElecDegree < 0) {
                    this.currenEle = 0;
                } else {
                    this.currenEle = this.currentInfo.elecDegree - this.currentInfo.preElecDegree;
                }
                if (this.currentInfo.waterDegree - this.currentInfo.preWaterDegree < 0) {
                    this.currenWater = 0;
                } else {
                    this.currenWater = this.currentInfo.waterDegree - this.currentInfo.preWaterDegree;
                }
                if (this.currentInfo.gasDegree - this.currentInfo.preGasDegree < 0) {
                    this.currenGas = 0;
                } else {
                    this.currenGas = this.currentInfo.gasDegree - this.currentInfo.preGasDegree;
                }
                this.getBusinessPayment();
            });
        },
        // 当前信息
        getCurrentInfo(id) {
            GetBusinessLifeCostBill(id).done((res) => {
                this.getRecord();
                this.currentInfo = res.data;
                if (JSON.parse(localStorage.getItem('life_fee'))) {
                    this.localInfo = JSON.parse(localStorage.getItem('life_fee'));
                    (this.currentInfo.waterDegree = this.localInfo.waterDegree),
                        (this.currentInfo.elecDegree = this.localInfo.elecDegree),
                        (this.currentInfo.gasDegree = this.localInfo.gasDegree),
                        (this.currentInfo.parkPrice = this.localInfo.parkPrice),
                        (this.currentInfo.propPrice = this.localInfo.propPrice),
                        (this.currentInfo.rubbishPrice = this.localInfo.rubbishPrice);
                }
                if (this.currentInfo.elecDegree - this.currentInfo.preElecDegree < 0) {
                    this.currenEle = 0;
                } else {
                    this.currenEle = this.currentInfo.elecDegree - this.currentInfo.preElecDegree;
                }
                if (this.currentInfo.waterDegree - this.currentInfo.preWaterDegree < 0) {
                    this.currenWater = 0;
                } else {
                    this.currenWater = this.currentInfo.waterDegree - this.currentInfo.preWaterDegree;
                }
                if (this.currentInfo.gasDegree - this.currentInfo.preGasDegree < 0) {
                    this.currenGas = 0;
                } else {
                    this.currenGas = this.currentInfo.gasDegree - this.currentInfo.preGasDegree;
                }
                this.getBusinessPayment();
            });
        },
        // 数据查询
        getBusinessPayment() {
            GetSystemDictDataByType({ dictType: 'business_payment_offline' }).done((res) => {
                this.paymentsAll = res.data;
                this.paymentsColumns = res.data.map((item) => item.dictLabel);
            });
            GetSystemDictDataByType({ dictType: 'business_payment' }).done((res) => {
                if (this.currentInfo.payStatus == '1') {
                    this.payment_label = res.data.find((item) => item.dictValue == this.currentInfo.payment).dictLabel;
                }
            });
            GetBusinessHotelPayeeAccountByhotel({ hotelId: this.hotelId }).done((res) => {
                this.userAll = res.data.filter((item) => item.payment >= '5' || item.account);
                this.users = res.data.filter((item) => item.payment >= '5' || item.account).map((item) => item.accountName);
                this.payeeAccountIdValue = this.users.length > 0 ? null : '暂无';
            });
        },
        // 换房记录
        getRecord() {
            GetBusinessLifeCostRoomRecordByLifeCostBill(this.$route.query.lifeCostBillId).done((res) => {
                this.recordRoom = res.data.filter((item) => item.meterType == 0);
                this.recordRoom.forEach((item) => {
                    item.ele_totalPrice = (item.elecDegree - item.preElecDegree) * this.currentInfo.elecPrice;
                    item.water_totalPrice = (item.waterDegree - item.preWaterDegree) * this.currentInfo.waterPrice;
                    item.gas_totalPrice = (item.gasDegree - item.preGasDegree) * this.currentInfo.gasPrice;
                    item.roomP = this.currentInfo.elecPrice ? Number(item.ele_totalPrice) : 0 + this.currentInfo.waterPrice ? Number(item.water_totalPrice) : 0 + this.currentInfo.gasPrice ? Number(item.gas_totalPrice) : 0;
                });
                this.roomT = 0;
                this.recordRoom.forEach((val) => (this.roomT += val.roomP));
                this.recordWatch = res.data.filter((item) => item.meterType != 0);
                this.recordWatch.forEach((item) => {
                    item.ele_totalPrice = (item.elecDegree - item.preElecDegree) * this.currentInfo.elecPrice;
                    item.water_totalPrice = (item.waterDegree - item.preWaterDegree) * this.currentInfo.waterPrice;
                    item.gas_totalPrice = (item.gasDegree - item.preGasDegree) * this.currentInfo.gasPrice;
                    item.watchP = item.meterType == 1 ? item.ele_totalPrice : 0 + item.meterType == 2 ? item.water_totalPrice : 0 + item.meterType == 3 ? item.gas_totalPrice : 0;
                });
                this.waterT = 0;
                this.recordWatch.forEach((val) => (this.waterT += val.watchP));
            });
        },
        // 编辑生活账单
        LiveEdit() {
            if (!this.$route.query.cooperLifeCostBillId) {
                this.$router.push({
                    path: '/editLifeFee',
                    query: {
                        lifeCostBillId: this.$route.query.lifeCostBillId
                    }
                });
            } else {
                this.$router.push({
                    path: '/editLifeFee',
                    query: {
                        cooperLifeCostBillId: this.$route.query.cooperLifeCostBillId
                    }
                });
            }
        },
        // 提交账单
        subBill() {
            let params = {
                id: this.$route.query.lifeCostBillId,
                waterDegree: this.currentInfo.waterDegree,
                elecDegree: this.currentInfo.elecDegree,
                gasDegree: this.currentInfo.gasDegree,
                propPrice: this.currentInfo.propPrice,
                rubbishPrice: this.currentInfo.rubbishPrice,
                parkPrice: this.currentInfo.parkPrice
            };
            MessageBox.confirm('', {
                message: '确认提交账单？',
                title: '提示',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        PostBusinessLifeCostBillEdit(params).done((res) => {
                            Toast({
                                message: res.msg,
                                duration: 900
                            });
                            localStorage.setItem('life_fee', JSON.stringify(res.data));
                            setTimeout('window.history.go(0)', 900);
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 选择到账时间
        onConfirmTime(date) {
            this.comeTime = dayjs(date).format('YYYY-MM-DD');
            this.comeShowTime = false;
        },
        // 收款账户
        onConfirmUser(val) {
            this.payeeAccountIdValue = val;
            this.payeeAccountId = this.userAll.find((item) => item.accountName == val).id;
            this.payeeAccountIdValueShow = false;
        },
        // 确定
        mySave() {
            if (!this.payeeAccountId) {
                Toast({
                    message: '请选择收款账户',
                    duration: 900
                });
                return;
            }
            let data = {
                payTime: this.comeTime,
                payeeAccountId: this.payeeAccountId
            };
            let _api = this.$route.query.lifeCostBillId ? PostBusinessLifeCostBillPayOffline : PostBusinessCoopeLifeCostBillPayOffline;
            _api(this.currentInfo.id, data).done((res) => {
                this.comeShow = false;
                Toast({
                    message: res.msg,
                    duration: 900
                });
                if (this.$route.query.cooperLifeCostBillId) {
                    this.getCooperCurrentInfo(this.$route.query.cooperLifeCostBillId);
                } else {
                    this.getCurrentInfo(this.$route.query.lifeCostBillId);
                }
            });
        },
        // 撤回账单-普通
        backBill() {
            PostBusinessLifeCostBillRecall(this.$route.query.lifeCostBillId).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                localStorage.setItem('life_fee', JSON.stringify(res.data));
                setTimeout('window.history.go(0)', 900);
            });
        },
        // 提交生活总账单
        backBillCooperSubmit() {
            PostBusinessCooperLifeCostBillSubmit({ cooperLifeCostBillId: this.$route.query.cooperLifeCostBillId }).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.getCooperCurrentInfo(this.$route.query.cooperLifeCostBillId);
            });
        },
        // 单位订单撤回
        backBillCooper() {
            PostBusinessCooperLifeCostBillRecall({ cooperLifeCostBillId: this.$route.query.cooperLifeCostBillId }).done((res) => {
                this.getCooperCurrentInfo(this.$route.query.cooperLifeCostBillId);
            });
        },
        // 收款码
        getCode(item) {
            this.$router.push({
                path: '/roomCode-page',
                query: {
                    roomPrice: item.totalPrice,
                    billSn: item.lifeCostSn,
                    payStatus: item.payStatus
                }
            });
        },
        confirmDelete(action, done) {
            if (action == 'confirm') {
                done(false);
                PostBusinessLifeCostBillRemove(this.currentInfo.id, { remark: this.deleteRemarks }).done((res) => {
                    Toast(res.msg);
                    setTimeout('window.history.go(-1)', 900);
                });
                this.showDelete = false;
            } else if (action == 'cancel') {
                done(false);
                this.showDelete = false;
            }
        }
    }
};
</script>
